// let center = [56.34105599356019, 43.817078808528905];
let center = [56.34100750797671,43.81700269503323];

function init() {
  let map = new ymaps.Map('map', {
    center: center,
    zoom: 18
  });

  let placemark = new ymaps.Placemark(center, {
    balloonContentHeader: 'Хедер балуна',
    balloonContentBody: 'Боди балуна',
    balloonContentFooter: 'Футер балуна',
  }, {
    iconLayout: 'default#image',
    iconImageSize: [60, 60],
    iconImageOffset: [-19, -44]
  });

  let placemark1 = new ymaps.Placemark(center, {
    balloonContent: `

    <div class="balloon">
      <h4 class="balloon__title">Контакты</h4>
      <ul class=" list-reset balloon__list">
        <li class="balloon__item-tel">
          <a class="balloon__link" href="tel:+79506165757">+7 950 616-57-57</a>
        </li>
        <li class="balloon__item-mail">
          <a class="balloon__link" href="mailto:plitkaikamen-nn@yandex.ru">plitkaikamen-nn@yandex.ru</a>
        </li>
        <li class="balloon__item-address">
          <address class="balloon__item-street">г. Нижний Новгород, ул. Федосеенко, 45</address>
        </li>
        <li class="balloon__item-time">
          <p class="balloon__item-time-text">Пн-Пт: c 9:00–20:00,</p>
          <p class="balloon__item-time-text">Сб-Вс: 10:00–18:00</p>
        </li>
      </ul>
		</div>

    `,
  }, {
    iconLayout: 'default#image',
    iconImageSize: [60, 60],
    iconImageOffset: [-19, -44]
  });

  map.controls.remove('geolocationControl'); // удаляем геолокацию
  map.controls.remove('searchControl'); // удаляем поиск
  map.controls.remove('trafficControl'); // удаляем контроль трафика
  map.controls.remove('typeSelector'); // удаляем тип
  map.controls.remove('fullscreenControl'); // удаляем кнопку перехода в полноэкранный режим
  map.controls.remove('zoomControl'); // удаляем контрол зуммирования
  map.controls.remove('rulerControl'); // удаляем контрол правил

  // map.geoObjects.add(placemark);
  map.geoObjects.add(placemark1);
  placemark1.balloon.open();
}

ymaps.ready(init);
