const about = document.querySelector('.about');

if (about) {

  const play = about.querySelector('.video__play');
  const video = about.querySelector('.video video');
  const videoBody = about.querySelector('.video');

  play.addEventListener('click', () => {
    video.play();
    video.setAttribute('controls', 'controls');
    play.classList.add('video__play--hidden');
    videoBody.classList.remove('video--visible');
  });
}
