// Подключение свайпера
import Swiper, {
  Navigation,
  Pagination
} from 'swiper';
Swiper.use([Navigation, Pagination]);

const swipersNew = document.querySelector('.new__content');
const swipersPost = document.querySelector('.post__content');
const swipersGal = document.querySelector('.gallery__content');

if (swipersNew) {
  const swiperNew = new Swiper(swipersNew, {
    slidesPerView: 4,
    spaceBetween: 40,
    navigation: {
      nextEl: '.new__slider-btn--next',
      prevEl: '.new__slider-btn--prev',
    },
    pagination: {
      el: '.new__pagination',
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {
      // when window width is >= 320px
      320: {
        slidesPerView: 1,
        spaceBetween: 20
      },
      // when window width is >= 480px
      630: {
        slidesPerView: 2,
        spaceBetween: 30
      },
      1366: {
        slidesPerView: 3,
        spaceBetween: 40
      },
      // when window width is >= 640px
      1921: {
        slidesPerView: 3,
        spaceBetween: 40
      },
      2041: {
        slidesPerView: 4,
        spaceBetween: 40
      }
    }
  });
};

