const product = document.querySelector('.product');

if (product) {

  const items = product.querySelectorAll(".option-checkbox");
  const button = product.querySelector("#button");

  function onResize() {
    let count = 0;
    if (window.innerWidth <= 576) {
      count = 3;
    }
    if (window.innerWidth >= 576 && window.innerWidth <= 1921) {
      count = 13;
    }
    if (window.innerWidth >= 1921) {
      count = 15;
    }
    if (window.innerWidth >= 2048) {
      count = 23;
      if (items.length <= 3) {
        button.style.display = "none";
      }
    }
    return count;
  }

  window.addEventListener("resize", onResize);
  button.addEventListener("click", clickHandler);

  let countD = onResize();

  function clickHandler() {
    if (window.innerWidth <= 576) {
      countD += 3;
    }
    if (window.innerWidth >= 576 && window.innerWidth <= 1921) {
      countD += 13;
    }
    if (window.innerWidth >= 1921) {
      countD += 15;
    }
    if (window.innerWidth >= 2048) {
      countD += 23;
    }

    if (countD <= items.length || countD >= items.length) {
      for (let i = 0; i < countD; i++) {
        if (i !== items.length) {
          items[i].style.display = "block";
        } else {
          return (button.style.display = "none");
        }
      }
    }
  }


  const itemsCh = product.querySelectorAll(".product-checkbox");
  const buttonCh = product.querySelector("#button-checkbox");

  function onResizeCh() {
    let countCh = 0;
    if (window.innerWidth <= 576) {
      countCh = 3;
    }
    if (window.innerWidth >= 576 && window.innerWidth <= 1921) {
      countCh = 13;
    }
    if (window.innerWidth >= 1921) {
      countCh = 15;
    }
    if (window.innerWidth >= 2048) {
      countCh = 23;
      if (itemsCh.length <= 3) {
        buttonCh.style.display = "none";
      }
    }
    return countCh;
  }

  window.addEventListener("resize", onResizeCh);
  buttonCh.addEventListener("click", clickHandlerCh);

  let countDCh = onResizeCh();

  function clickHandlerCh() {
    if (window.innerWidth <= 576) {
      countDCh += 3;
    }
    if (window.innerWidth >= 576 && window.innerWidth <= 1921) {
      countDCh += 13;
    }
    if (window.innerWidth >= 1921) {
      countDCh += 15;
    }
    if (window.innerWidth >= 2048) {
      countDCh += 23;
    }

    if (countDCh <= itemsCh.length || countDCh >= itemsCh.length) {
      for (let i = 0; i < countDCh; i++) {
        if (i !== itemsCh.length) {
          itemsCh[i].style.display = "block";
        } else {
          return (buttonCh.style.display = "none");
        }
      }
    }
  }
}
